import DataService from "../../service/DataService";

let buttons = [
  {
    title: "Add Check Out Request",
    iconClass: "fa fa-plus",
    callbackfunction: "AddCheckOut",
    additionalClass: "",
    isdisabled: false,
    isvisible: true,
    checkPrivilege: false,
  },
];

let leftsearchSchema = [
  {
    fieldName: "Product Name",
    fieldType: "radio",
    fieldIcon: "fa-user",
    paramName: "catalogId",
    fieldSearchConditionName: [""],
    isSearch: false,
    value: "",
    listOptions: [],
  },
  {
    fieldName: "Unique Name",
    fieldType: "ddl-check",
    fieldIcon: "fa-server",
    paramName: "UniqueNames",
    fieldSearchConditionName: [""],
    isSearch: false,
    value: "",
    listOptions: [],
  },
];

let listActions = [
  {
    title: "Reject",
    href: "",
    icon: "fa fa-times-circle action-icon text-danger",
    additionalSpan: "",
  },
];

function bindleftCommonSearchdropdown(instance, modulename, filtername) {
  var vm = instance;
  if (filtername == "Product Name") {
    DataService.GetAssetData().then((response) => {
      vm.json = response.data.data;
      if (vm.json != null) {
        vm.json.forEach((item) => {
          let obj = {
            name: item.NAME,
            value: item.ASSETID,
          };
          vm.leftSearchFields[0].listOptions.push(obj);
        });
        return;
      }
    });
  }
  if (filtername == "Unique Name") {
    var url = `assetCatalogId=&name=&issueTo=&locationId=&requestFrom=ALL&assetType=`;
    DataService.GetUniqueAssetItems(url).then((response) => {
      response.data.forEach(element => {
       let obj = {
          name: element.Name,
          value: element.Value
      }
        vm.leftSearchFields[1].listOptions.push(obj);
    });
      // vm.json = response.data;
      // if (vm.json != null) {
      //   vm.json.forEach((item) => {
      //     let obj = {
      //       name: item.ASSETTYPE,
      //       value: item.ASSETID,
      //     };
      //     vm.leftSearchFields[1].listOptions.push(obj);
      //   });
      //   return;
      // }
    });
  }
}

export default {
  buttons,
  leftsearchSchema,
  listActions,
  bindleftCommonSearchdropdown,
};
